import React, { useState } from 'react';
import './OrderDetails.css';
import Navbar from './Navbar';

const OrderDetails = () => {
  const [orders, setOrders] = useState([
    {
      id: 'ORD001',
      date: '2024-07-26',
      totalAmount: 35,
      customer: {
        name: 'John Doe',
        email: 'john.doe@example.com',
        phone: '123-456-7890',
      },
      medicine: [
        { name: 'Aspirin', quantity: 2, price: 10 },
        { name: 'Ibuprofen', quantity: 1, price: 15 },
      ],
    },
    {
      id: 'ORD002',
      date: '2024-07-25',
      totalAmount: 30,
      customer: {
        name: 'Jane Smith',
        email: 'jane.smith@example.com',
        phone: '234-567-8901',
      },
      medicine: [
        { name: 'Paracetamol', quantity: 5, price: 20 },
      ],
    },
    {
      id: 'ORD003',
      date: '2024-07-24',
      totalAmount: 28,
      customer: {
        name: 'Alice Johnson',
        email: 'alice.johnson@example.com',
        phone: '345-678-9012',
      },
      medicine: [
        { name: 'Amoxicillin', quantity: 3, price: 25 },
      ],
    },
  ]);

  const [newOrder, setNewOrder] = useState({
    id: '',
    date: '',
    totalAmount: '',
    customerName: '',
    email: '',
    phone: '',
    medicineName: '',
    quantity: '',
    price: '',
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewOrder({ ...newOrder, [name]: value });
  };

  const addOrder = () => {
    if (
      newOrder.id &&
      newOrder.date &&
      newOrder.totalAmount &&
      newOrder.customerName &&
      newOrder.email &&
      newOrder.phone &&
      newOrder.medicineName &&
      newOrder.quantity &&
      newOrder.price
    ) {
      setOrders([...orders, {
        id: newOrder.id,
        date: newOrder.date,
        totalAmount: parseFloat(newOrder.totalAmount),
        customer: {
          name: newOrder.customerName,
          email: newOrder.email,
          phone: newOrder.phone,
        },
        medicine: [
          { name: newOrder.medicineName, quantity: parseInt(newOrder.quantity), price: parseFloat(newOrder.price) }
        ],
      }]);
      setNewOrder({
        id: '',
        date: '',
        totalAmount: '',
        customerName: '',
        email: '',
        phone: '',
        medicineName: '',
        quantity: '',
        price: '',
      });
    }
  };

  return (
    <div className="order-details">
        <div>
            <Navbar/>
        </div>
      <h2>Order Details</h2>
      <div className="add-order-form">
        <input
          type="text"
          name="id"
          placeholder="Order ID"
          value={newOrder.id}
          onChange={handleInputChange}
        />
        <input
          type="date"
          name="date"
          placeholder="Date"
          value={newOrder.date}
          onChange={handleInputChange}
        />
        <input
          type="number"
          name="totalAmount"
          placeholder="Total Amount"
          value={newOrder.totalAmount}
          onChange={handleInputChange}
        />
        <input
          type="text"
          name="customerName"
          placeholder="Customer Name"
          value={newOrder.customerName}
          onChange={handleInputChange}
        />
        <input
          type="email"
          name="email"
          placeholder="Email"
          value={newOrder.email}
          onChange={handleInputChange}
        />
        <input
          type="text"
          name="phone"
          placeholder="Phone"
          value={newOrder.phone}
          onChange={handleInputChange}
        />
        <input
          type="text"
          name="medicineName"
          placeholder="Medicine Name"
          value={newOrder.medicineName}
          onChange={handleInputChange}
        />
        <input
          type="number"
          name="quantity"
          placeholder="Quantity"
          value={newOrder.quantity}
          onChange={handleInputChange}
        />
        <input
          type="number"
          name="price"
          placeholder="Price"
          value={newOrder.price}
          onChange={handleInputChange}
        />
        <button onClick={addOrder}>Add Order</button>
      </div>
      <div className="orders-container">
        <div className="order-header">
          <span>Order ID</span>
          <span>Customer Name</span>
          <span>Email</span>
          <span>Phone</span>
          <span>Date</span>
          <span>Total Amount</span>
          <span>Medicines</span>
        </div>
        {orders.map((order) => (
          <div key={order.id} className="order-row">
            <span>{order.id}</span>
            <span>{order.customer.name}</span>
            <span>{order.customer.email}</span>
            <span>{order.customer.phone}</span>
            <span>{order.date}</span>
            <span>${order.totalAmount.toFixed(2)}</span>
            <span>
              {order.medicine.map((med, index) => (
                <div key={index} className="medicine-item">
                  <span>{med.name} (x{med.quantity}) - ${med.price.toFixed(2)}</span>
                </div>
              ))}
            </span>
          </div>
        ))}
      </div>
    </div>
  );
};

export default OrderDetails;
