import React, { useState } from 'react';
import './StoreDetails.css';
import Navbar from './Navbar';

const StoreDetails = () => {
  const [stores, setStores] = useState([
    { id: 1, storeName: 'Health Plus', stock: 120, orders: 45, location: '123 Elm St', contact: '123-456-7890' },
    { id: 2, storeName: 'MediCare', stock: 80, orders: 30, location: '456 Maple St', contact: '987-654-3210' },
    { id: 3, storeName: 'PharmaLife', stock: 150, orders: 60, location: '789 Oak St', contact: '456-789-1230' }
  ]);
  const [newStore, setNewStore] = useState({
    storeName: '',
    stock: '',
    orders: '',
    location: '',
    contact: '',
  });
  const [error, setError] = useState('');

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewStore({ ...newStore, [name]: value });
  };

  const addStore = () => {
    if (!newStore.storeName || !newStore.stock || !newStore.orders || !newStore.location || !newStore.contact) {
      setError('All fields are required.');
      return;
    }

    setStores([...stores, { ...newStore, id: stores.length + 1 }]);
    setNewStore({
      storeName: '',
      stock: '',
      orders: '',
      location: '',
      contact: '',
    });
    setError('');
  };

  const handleOrder = (id) => {
    setStores(stores.map((store) =>
      store.id === id && store.stock > 0
        ? { ...store, stock: store.stock - 1, orders: store.orders + 1 }
        : store
    ));
  };

  return (
    <div className="store-details">
      <div>
        <Navbar/>
      </div>
      <h2>Store Details</h2>
      <div className="add-store-form">
        <input
          type="text"
          name="storeName"
          placeholder="Store Name"
          value={newStore.storeName}
          onChange={handleInputChange}
        />
        <input
          type="number"
          name="stock"
          placeholder="Stock"
          value={newStore.stock}
          onChange={handleInputChange}
        />
        <input
          type="number"
          name="orders"
          placeholder="Orders"
          value={newStore.orders}
          onChange={handleInputChange}
        />
        <input
          type="text"
          name="location"
          placeholder="Location"
          value={newStore.location}
          onChange={handleInputChange}
        />
        <input
          type="text"
          name="contact"
          placeholder="Contact"
          value={newStore.contact}
          onChange={handleInputChange}
        />
        <button onClick={addStore}>Add Store</button>
        {error && <p className="error">{error}</p>}
      </div>

      <div className="store-list">
        <div className="store-header">
          <div className="store-column">Store Name</div>
          <div className="store-column">Stock</div>
          <div className="store-column">Orders</div>
          <div className="store-column">Location</div>
          <div className="store-column">Contact</div>
          <div className="store-column">Action</div>
        </div>
        {stores.map((store) => (
          <div key={store.id} className="store-item">
            <div className="store-column">{store.storeName}</div>
            <div className="store-column">{store.stock}</div>
            <div className="store-column">{store.orders}</div>
            <div className="store-column">{store.location}</div>
            <div className="store-column">{store.contact}</div>
            <div className="store-column">
              <button onClick={() => handleOrder(store.id)} disabled={store.stock <= 0}>Order</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default StoreDetails;
