import React, { useState } from 'react';
import './MedicinePage.css';
import Cart from './Cart';
import { Link } from 'react-router-dom';
import UserNavbar from './UserNavbar';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const MedicinePage = () => {
  const [cart, setCart] = useState([]);
  const [showCart, setShowCart] = useState(false);

  const medicines = [
    { id: 1, image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTrGxkrHNMpuYZsr91pzg9eyruMxeUehMlLLg&s', name: 'Paracetamol', description: 'Used to treat fever and pain.', price: 5 },
    { id: 2, image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQIrrJolhCdo7hc7hoqAr715764k-BMhTwmag&s', name: 'Loratadine', description: 'Used to relieve allergy symptoms.', price: 7 },
    { id: 3, image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ94Y0FvaT_oFLiXInX7BxFVcgI1i07RD3gnw&s', name: 'Metformin', description: 'Used to manage type 2 diabetes.', price: 12 },
    { id: 4, image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRpL9jsfBqEK5zjPEJ6vo8ukhSUlTvN7LQDUQ&s', name: 'Atorvastatin', description: 'Used to lower cholesterol.', price: 15 },
    { id: 5, image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRzcXK0Ta6seVrgj6jJ0eR0utcPZaVhBBnP7A&s', name: 'Amoxicillin', description: 'Antibiotic used to treat bacterial infections.', price: 10 },
    { id: 6, image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQbM4ZHJiU39-TI6N-g54q4qJM2DkFVXEFTLw&s', name: 'Omeprazole', description: 'Used to treat acid reflux.', price: 9 },
    { id: 7, image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSa2HHnzuzRmuQI9Fa6SrNU_GCDrmAOhAUkgw&s', name: 'Hydrochlorothiazide', description: 'Used to treat high blood pressure.', price: 11 },
    { id: 8, image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQWbkKX2NUDzpsTtpLRSm2fl0o0Uxd4QhtuXA&s', name: 'Clonazepam', description: 'Used to treat panic disorders.', price: 14 },
    { id: 9, image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTgVqanwONb2op-3RvR3ssEU1ppYSBQFPFj1g&s', name: 'Losartan', description: 'Used to treat high blood pressure.', price: 12 },
    { id: 12, image: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQ4dSgTB983ooZCP5vQAdNs1ME1YvyzUgASIw&s', name: 'Furosemide', description: 'Used to treat fluid retention.', price: 11 },
  ];

  const addToCart = (medicine) => {
    setCart((prevCart) => {
      const existingItemIndex = prevCart.findIndex((item) => item.id === medicine.id);
      if (existingItemIndex > -1) {
        // Item already in cart, update quantity and total price
        const updatedCart = [...prevCart];
        const existingItem = updatedCart[existingItemIndex];
        updatedCart[existingItemIndex] = {
          ...existingItem,
          quantity: existingItem.quantity + 1,
          totalPrice: existingItem.totalPrice + medicine.price
        };
        toast.success(`${medicine.name} added to cart!`);
        return updatedCart;
      }
      // Add new item to cart
      toast.success(`${medicine.name} added to cart!`);
      return [...prevCart, { ...medicine, quantity: 1, totalPrice: medicine.price }];
    });
  };

  const removeFromCart = (id) => {
    setCart((prevCart) => prevCart.filter((item) => item.id !== id));
  };

  return (
    <div className="medicine-page">
      <UserNavbar />
      <div className='medi_move'>
        <header className="medicine-header">
          <h1>Our Medicines</h1>
          <button onClick={() => setShowCart(!showCart)} className="show-cart-btn">
            {showCart ? 'Hide Cart' : 'Show Cart'}
          </button>
          <Link to="/userorderdetails" state={{ cart }} className='medi_hide'>
            <button>Order Details</button>
          </Link>
        </header>
        {showCart && <Cart cart={cart} removeFromCart={removeFromCart} />}
        <div className="medicine-list">
          {medicines.length > 0 ? (
            medicines.map((medicine) => (
              <div className="medicine-card" key={medicine.id}>
                <img src={medicine.image} alt={medicine.name} className="medicine-image" />
                <div className="medicine-info">
                  <h2>{medicine.name}</h2>
                  <p>{medicine.description}</p>
                  <div className="medicine-pricing">
                    <span className="medicine-price">${medicine.price}</span>
                  </div>
                  <button className="order-btn" onClick={() => addToCart(medicine)}>
                    Order
                  </button>
                </div>
              </div>
            ))
          ) : (
            <p>No medicines available.</p>
          )}
        </div>
      </div>
      <ToastContainer />
    </div>
  );
};

export default MedicinePage;
