import React, { useState } from 'react';
import './Inventory.css'; 
import Navbar from './Navbar';
export default function Inventory() {
  const [medicines, setMedicines] = useState([
    { id: '1', name: 'Aspirin', power: '500mg', category: 'Pain Reliever', type: 'Tablet', price: '10', stock: '100' },
    { id: '2', name: 'Amoxicillin', power: '250mg', category: 'Antibiotic', type: 'Capsule', price: '20', stock: '50' },
    { id: '3', name: 'Paracetamol', power: '650mg', category: 'Fever Reducer', type: 'Tablet', price: '15', stock: '75' },
  ]);

  const [newMedicine, setNewMedicine] = useState({
    name: '',
    power: '',
    category: '',
    type: '',
    price: '',
    stock: '',
  });

  const handleAddMedicine = () => {
    if (Object.values(newMedicine).every(field => field.trim())) {
      setMedicines([...medicines, { id: (medicines.length + 1).toString(), ...newMedicine }]);
      setNewMedicine({ name: '', power: '', category: '', type: '', price: '', stock: '' });
    } else {
      alert('Please fill out all fields!');
    }
  };

  const handleDeleteButton = (id) => {
    setMedicines(medicines.filter(medicine => medicine.id !== id));
  };

  return (
    <div className="inventory-container">
        <div>
            <Navbar/>
        </div>
      <h4 className="page-title">Medicine Inventory</h4>
      <div className="add-medicine">

        <h5>Add New Medicine</h5>
        <div className="form-group">
          <input
            
            type="text"
            placeholder="Medicine Name"
            value={newMedicine.name}
            onChange={(e) => setNewMedicine({ ...newMedicine, name: e.target.value })}
          />
          <input
            type="text"
            placeholder="Power"
            value={newMedicine.power}
            onChange={(e) => setNewMedicine({ ...newMedicine, power: e.target.value })}
          />
          <input
            type="text"
            placeholder="Category"
            value={newMedicine.category}
            onChange={(e) => setNewMedicine({ ...newMedicine, category: e.target.value })}
          />
          <input
            type="text"
            placeholder="Type"
            value={newMedicine.type}
            onChange={(e) => setNewMedicine({ ...newMedicine, type: e.target.value })}
          />
          <input
            type="number"
            placeholder="Price"
            value={newMedicine.price}
            onChange={(e) => setNewMedicine({ ...newMedicine, price: e.target.value })}
          />
          <input
            type="number"
            placeholder="Stock"
            value={newMedicine.stock}
            onChange={(e) => setNewMedicine({ ...newMedicine, stock: e.target.value })}
          />
          <button onClick={handleAddMedicine}>Add Medicine</button>
        </div>
      </div>
      <table className="inventory-table">
        <thead>
          <tr>
            <th>S.No</th>
            <th>Medicine Name<sup>Power</sup></th>
            <th>Medicine Category</th>
            <th>Medicine Type</th>
            <th>Medicine Price</th>
            <th>Stock</th>
            <th>Action</th>
          </tr>
        </thead>
        <tbody>
          {medicines.map((medicine, index) => (
            <tr key={medicine.id}>
              <td>{index + 1}</td>
              <td>{medicine.name} <sup>{medicine.power}</sup></td>
              <td>{medicine.category}</td>
              <td>{medicine.type}</td>
              <td>{medicine.price}</td>
              <td>{medicine.stock}</td>
              <td>
                <button className="btn btn-danger" onClick={() => handleDeleteButton(medicine.id)}>Delete</button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
